




















import Vue from 'vue'
import ConfirmDialog from '@/dialogs/Confirm.vue'

let action = () => {}

export default Vue.extend({
  components: {
    ConfirmDialog
  },
  props: {
    dismissable: {
      type: Boolean,
      required: false,
      default: true
    }
  },
  data() {
    return {
      confirm: false
    }
  },
  methods: {
    setConfirmAction(a: () => void) {
      action = a
      this.confirm = !this.confirm
    },
    ignoreAction() {
      action = () => {}
      this.confirm = !this.confirm
    },
    executeAction() {
      this.confirm = !this.confirm
      action()
      action = () => {}
    }
  }
})
